.mobile
	min-height: 864px
	padding: 48px 0 79px 0
	.row
		margin-top: 32px/* отодвинул картинки от заголовка */
	&_item/* РАЗМЕР КАРТИНОК С ОТСТУПАМИ ТЕКСТА */
		position: relative
		width: 320px
		height: 280px
		margin: 0 auto
		margin-bottom: 19px/* рассоеденил картинки */
		padding-top: 92px/* отступ текста */
		&_1
			background: url('../img/mobile_item_1.png') center (center / cover) no-repeat
		&_2
			background: url('../img/mobile_item_2.jpg') center (center / cover) no-repeat
		&_3
			background: url('../img/mobile_item_3.jpg') center (center / cover) no-repeat
		&_4
			background: url('../img/mobile_item_4.jpg') center (center / cover) no-repeat
		&_5
			background: url('../img/mobile_item_5.jpg') center (center / cover) no-repeat
		&_6
			background: url('../img/mobile_item_6.jpg') center (center / cover) no-repeat
		&_subtitle /* стилизация текста в картинке, без отступов (отступ прописал выше) */
			color: #ffffff
			font-size: 24px
			font-weight: 400
			line-height: 32px
			text-transform: uppercase
			text-align: center
		&_plus/* стилизация крестика */
			display: flex
			justify-content: center
			align-items: center
			position: absolute
			bottom: 35px
			left: 50%/* встал по центру с погрешностью вправо */
			transform: translateX(-50%)/* отодвинул левее,встал четко по центру, почему: потому-что это половина ширины моего плюса */
			width: 26px
			height: 26px
			background-color: #000000
			margin: 0 auto
			border-radius: 100%
			cursor: pointer/* при наведении меняется курсор и плюс не зависит от моего заголовка */
			&:before
				content: ''/* content - обязательный элемент, если в ковычках ничего не написано, то они должны быть пустыми */
				display: block
				width: 14px
				height: 2px
				background-color: $main-color
				transition: 0.3s all/* при наведении задержка изменения цвета на 3 секунды */
			&:after
				content: ''/* content - обязательный элемент, если в ковычках ничего не написано, то они должны быть пустыми */
				position: absolute
				/* transform: translateX(-50%) *//* сдвинул вертикальную линию, по оси Х, на 50% его ширины */
				display: block
				width: 2px
				height: 14px
				background-color: $main-color
				transition: 0.3s all/* при наведении задержка изменения цвета на 3 секунды */
			&:hover /* при наведении мой плюс менял цвет */
				&:before, &:after
					background-color: $second-color

