.require
	padding: 68px 0 84px 0
	&_block
		margin: 33px 0/* установил границы сверху и снизу */
		&_nmb
			margin-bottom: 0px/* нет нижних границ */
	&_title
		color: #1ca8c3
		font-size: 22px
		font-weight: 400
	&_list	
		list-style-type: none/* убрал нумерацию, а именно кружки */
		padding-left: 50px/* отступ текста слева */
		margin-top: 22px
		li
			position: relative
			font-size: 17px
			font-weight: 300
			line-height: 24px
			margin-bottom: 10px
			&::before
				content: ''
				display: block
				position: absolute
				left: -32px/* сдвинул иконку левее */
				top: 3px/* сдвинул иконку ниже */
				width: 21px/* ширина иконки */
				height: 20px/* высота иконки */
				background: url('../icons/check_blue.svg') center (center / cover) no-repeat
	&_descr
		margin-top: 25px
		font-style: 17px
		font-weight: 300
		line-height: 24px
		color: #1ca8c3
	.warning
		.require_title
			color: $second-color
		.require_list
			li
				&::before
					background: url('../icons/check_red.svg') center (center / cover) no-repeat

.footer
	padding: 14px 23px 39px 23px
	&_divider/* создал горизонтальную линию, покрасил */
		width: 299px
		height: 4px
		background-color: #1fbad6
		margin: 0 auto
	&_wrepper
		display: flex
		justify-content: space-between
		align-items: flex-start
		margin-top: 32px
	&_wrepper > div
		width: 33%/* разделил экран на 3 части */
		&:nth-child(3)/* сдвинул правый блок вправо */
			display: flex
			justify-content: flex-end
	&_social
		width: 160px
		display: flex
		justify-content: space-between
		&_item
			display: flex
			justify-content: center
			align-items: center
			margin: 0 4px
			width: 32px
			height: 32px
			background-color: #070716
			border-radius: 100%
			.logo
				width: 20px
				height: 20px
